body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: whitesmoke;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



/* -----------------------------------------------Header----------------------------------------------- */

.header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 99;
  transition: all 0.5 ease;
  background-color: black;
  border-top: 2px solid lightgrey;
  border-bottom: 2px solid lightgrey;
  border-radius: 4em;
}

.header .fixed {
  background-color: white;
  position: fixed;
}

.header .brand-name {
  padding: 0 15px;

}

.header .brand-name a {
  text-decoration: none;
  font-size: 30px;
  color: white;
  text-transform: uppercase;
  font-weight: 900;
  letter-spacing: 2px;
  transition: all 0.5s ease;
  font-family: 'Chiller';
}

.header .fixed .brand-name a {
  color: black;
}

.header .nav-toggle {
  width: 40px;
  height: 34px;
  border: 1px solid white;
  border-radius: 3px;
  cursor: pointer;
  align-items: center;
  justify-content: center;
  transition: all 0.5s ease;
  display: none;

}

.home-badge {
  width: 55px;
  height: 55px;
}

.header .fixed .nav-toggle {
  border-color: black;
}

.header .nav-toggle span {
  height: 1px;
  display: block;
  width: 16px;
  background-color: white;
  position: relative;
  transition: all 0.5s ease;
}

.header .nav-toggle span::before,
.header .nav-toggle span::after {
  content: ' ';
  position: absolute;
  left: 0;
  top: 0;
  height: 1px;
  width: 100%;
  background-color: white;
  transition: all 0.5s ease;
}

.header .fixed .nav-toggle span,
.header .fixed .nav-toggle:before,
.header .fixed .nav-toggle:after {
  background-color: black;
}

.header .nav-toggle span:before {
  transform: translate(-6px);
}

.header .nav-toggle span:after {
  transform: translate(-6px);
}

.header .nav {
  padding: 0 15px;
}

.header .nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.header .nav ul li {
  display: inline-block;
  margin-left: 30px;
}

.header .nav ul li a {
  text-decoration: none;
  font-size: 18px;
  font-weight: 600;
  color: white;
  padding: 25px 0;
  display: block;
  transition: all 0.5s ease;
  font-family: 'Old English Text MT',
    'Engravers MT',
    'Times New Roman',
    sans-serif;
}

.header .fixed .nav ul li a {
  color: black;
}

.header .fixed .nav ul li a:hover,
.header .nav ul li a:hover {
  color: crimson;
}

/* ---------------------------------------------------Products----------------------------------------------- */

.market{
  margin-top: 8em;
}

.black-market-heading{
  text-align: center;
  font-family: 'Old English Text MT',
              'Engravers MT',
              'Times New Roman',
              sans-serif;
}

.products-div{
  text-align: center;
  display: grid;
  grid-template-columns: repeat(1, 900px);
  gap: 1em;
  justify-content: center;
  margin-top: 6em;
}

.product-block{
  border: 2px solid white;
  border-radius: 1em;
  background-color: white;
  padding: 3em 8em;
}

.product-name{
  font-family: 'Old English Text MT',
    'Engravers MT',
    'Times New Roman',
    sans-serif;
  font-size: 20px;
  text-align: left;
}

.product-img{
  width: 400px;
  height: 400px;
}

.product-info{
  display: grid;
  grid-template-columns: repeat(2, 20em);
  text-align: left;
}

.product-info h4{
  font-family: 'Old English Text MT',
    'Engravers MT',
    'Times New Roman',
    sans-serif ;
}

.product-description{
  font-family: 'Old English Text MT',
              'Engravers MT',
              'Times New Roman',
              sans-serif;
  color: black;
  text-align: right;
}

.product-shop-button{
  background-color: black;
  border-radius: 4em;
  color: white;
  font-family: 'Old English Text MT',
      'Engravers MT',
      'Times New Roman',
      sans-serif;
  width: 10em;
  height: 3em;
}

.product-shop-button:hover{
  background-color: darkred;
  cursor: pointer;
  transition: all 1s ease;
}

.product-shop-button-div{
  text-align: right;
}

.product-add-cart{
  background-color: lightgrey;
  margin: 0 1em;
}

.product-add-cart:hover{
  cursor: pointer;
}

@media screen and (max-width: 600px){

  .products-div{
    grid-template-columns: repeat(1, 300px);
  }

  .product-block {
    border: 2px solid white;
    border-radius: 1em;
    background-color: white;
    padding: 3em 2em;
  }

  .product-img {
    width: 200px;
    height: 200px;
  }

  .product-info {
    display: grid;
    grid-template-columns: repeat(2, 6em);
    text-align: left;
  }

  .product-add-cart {
    background-color: lightgrey;
    margin: 0;
  }
}